<template>
    <div class="SlideBar">
        <div class="author">
            <p class="topbar">作者</p>
            <router-link :to = "{
                name: 'user_info',
                params: {
                    name: userInfo.loginname
                }
            }">
                <img :src="userInfo.avatar_url" alt="">
            </router-link>
        </div>
        <div class="authorTopic">
            <p class="topbar">作者最近主题</p>
            <ul>
                <li v-for="list in replylimitby5" :key="list.value">
                    <router-link :to="{
          name:'user_info',
          params:{
            name:userInfo.loginname
          }
          }">
                        {{list.title}}
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="authorReply">
            <p class="topbar">作者最近回复</p>
            <ul>
                <li v-for="list in topiclimitby5" :key="list.value">
                    <router-link :to="{
          name:'user_info',
          params:{
            name:userInfo.loginname
          }
          }">
                        {{list.title}}
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SlideBar",
        data() {
            return {
                userInfo: {
                    recent_topics: [],
                    recent_replies: []
                },
            }
        },
        methods: {
            getUserInfoData() {
                this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
                    .then(res => {
                        console.log(1);
                        this.isLoading = false;
                        this.userInfo = res.data.data;
                    }).catch(err => {
                    console.log(err);
                })
            }
        },
        computed: {
            replylimitby5: function () {
                if (this.userInfo.recent_topics.length > 5) {
                    return this.userInfo.recent_topics.slice(0, 5)
                }else {
                  return this.userInfo.recent_topics
                }
        },
        
        topiclimitby5: function () {
            if (this.userInfo.recent_replies.length > 5) {
                return this.userInfo.recent_replies.slice(0, 5)
            }else {
              return this.userInfo.recent_replies
            }
        }
        },
        beforeMount(){
            // this.isLoading = true;//加载成功之前显示加载动画
            this.getUserInfoData();//在页面加载之前获取数据
        }
    };
</script>

<style scoped>
    .SlideBar {
        width: 20%;
        float: right;

    }
    .topbar {
        padding-top: 10px;
        padding-left: 10px;
        background-color: #f6f6f6;
        height: 35px;
        margin-bottom: 10px;
        font-size: 12px;
    }
    .author{
        background-color: #ffffff;
        padding-bottom: 10px;
        border-radius: 3px 3px 0 0;
        margin-left: 10px;
        margin-bottom: 10px;
    }
    .author img{
        width: 48px;
        height: 48px;
        margin-left: 10px;
    }
    .authorTopic {
      margin-left: 10px;
      margin-bottom: 10px;
      background-color: #ffffff;
    }
    .authorReply {
      margin-left: 10px;
        background-color: #ffffff;
        }
    ul a {
        font-size: 12px;
        text-decoration: none;
        margin: 10px 10px;
        color: #778087;
    }
    li {
      list-style: none;
      margin: 10px;
    }
</style>